.fadein-slow {
  animation-name: fadein;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.header {
  padding-top: 50px;
}

.header .top-menu .company-logo {
  animation-name: fadein;
  animation-duration: 1s;
  animation-delay: 2s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .top-menu .menu-items {
  animation-name: fadein;
  animation-duration: 2s;
  animation-delay: 5s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .hello-title {
  font-size: 5rem;
  font-weight: 600;
  line-height: 1.125;
  margin-bottom: 1rem;
  color: black;
  animation-name: fadein;
  animation-duration: 1s;
  animation-delay: .1s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .hello-message {
  animation-name: fadein;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .hello-get-in-touch {
  animation-name: fadein;
  animation-duration: 1s;
  animation-delay: 5s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .hello-see-you-later {
  animation-name: fadein;
  animation-duration: 2s;
  animation-delay: 5s;
  animation-timing-function: ease-in;
  animation-fill-mode: backwards;
}

.header .arrow-down {
  opacity: 0;
  animation-name: fadein;
  animation-delay: 2s;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: ease-in;
}

.hero-sketches {
  display: flex;
  width: 100%;
  height: 100%;
}

.hero-sketches .hero-sketch {
  width: 100%;
  max-height: 100%;
}

.hero-sketches .hero-sketch>* {
  fill-opacity: 0;
}

.services .service-icon {
  max-width: 50%;
}

.services hr {
  margin-top: 1rem;
  margin-bottom: 4rem;
  width: 10%;
  height: 0.2rem;
  margin-left: auto;
  margin-right: auto;
}

.highlight+.highlight {
  padding-top: 0;
}

.highlight hr {
  margin-top: 1rem;
  width: 20%;
  height: 0.2rem;
}

.highlight .logos .logo {
  max-width: 20%;
  margin: 1rem;
}

.highlight>.container {
  padding-left: 6rem;
  padding-right: 6rem;
}

.expertises .expertise {
  margin-bottom: 1.5rem;
}

.expertises .expertise .logo {
  margin-bottom: 1.25rem;
  max-width: 75px;
  max-height: 75px;
}

strong,
.title,
.subtitle {
  color: inherit;
}

section.section {
  padding-top: 8rem;
  padding-bottom: 8rem;
}

a {
  color: inherit;
}

.content ul>li {
  list-style-type: square;
}

